.sectionDesign, .sectionDevelopment {

  .headingTertiary {
    font-weight: 700;
    font-size: 2.5rem;
    color: var(--color-secondary);
    border-bottom: 2px solid var(--color-border);
    padding-bottom: 1.28rem;
  }

  .headingQuaternary {
    font-weight: 600;
    font-size: 2.0rem;
    color: var(--color-secondary);
    margin-top: 2.1rem;
  }

  ul {
    list-style: none;
  }

  .skillsContainer {

    .skills {
      margin-top: 2.1rem;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;

      .skillsTitle {
        font-weight: 600;
        font-size: 2.0rem;
        color: var(--color-secondary); 
        margin-bottom: 1.6rem;
        display: inline-block;

        @media only screen and (max-width: 41.25em) {
          margin-bottom: 0.4rem;

          &:first-child {
            margin-bottom: 1.6rem;
          }
        }
      }

      .skillsList {
        font-weight: 300;
        font-size: 2rem;
        color: var(--color-secondary);

        a:link,
        a:visited {
          text-decoration: none;
          color: inherit;
        }

        a:hover,
        a:active {
          border-bottom: 2px solid var(--color-primary);
        }
        
        &__row {
          display: flex;
          flex-wrap: wrap;
          margin-bottom: 1.6rem;

          li:not(:last-child) {
            margin-right: 1.6rem
          }

          @media only screen and (max-width: 41.25em) {
            flex-direction: column;
            li {
              margin-bottom: 1.28rem;
            }
          }
        }

        &__column {
          display: flex;
          flex-direction: column;
          margin-bottom: 1.6rem;

          li {
            margin-bottom: 1.28rem
          }

        }
      }
    }
  }
}

.sectionDevelopment {
  margin-top: 6.1rem;
}



/* -- Relevant Certifications -- */

.school {

  margin-bottom: 6.1rem;
  display: flex;
  flex-direction: column;
  
  &Course {
    font-weight: 600;
    font-size: 2.5rem;
    color: var(--color-secondary);
    margin-bottom: 1.28rem;
  }

  &Name {
    font-size: 2rem;
    font-style: italic;
    color: var(--color-secondary-light);

  }
}