.icon-logo {
  --logo-width: 6.1rem;
  width: var(--logo-width);
  height: calc(var(--logo-width) * 1.1147540984);
  fill: var(--color-dark);

  @media only screen and (max-width: 31.25em) {
    --logo-width: 5.2rem;
  }
}

.icon-hero {
  // width: 61rem;
  // height: 54.2rem;
  --hero-width: 60rem;
  width: calc(var(--hero-width) * 1.16);
  height: var(--hero-width);
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: -10;

  /* -- margin-right is dependent on the padding of body element -- */
  @media only screen and (max-width: 55em) {
    margin-right: -8rem;
    --hero-width: 70vw;
  }

  @media only screen and (max-width: 41.25em) {
    margin-right: -5rem;
    --hero-width: 65vw;
  }

  @media only screen and (max-width: 24em) {
    margin-right: -4rem;
    --hero-width: 70vw;
  }
}

.icon-about, .icon-skills, .icon-projects, .icon-contact {
  --icon-width: 3.9rem;
  width: var(--icon-width);
  height: var(--icon-width);

  @media only screen and (max-width: 30em) {
    --icon-width: 3rem;
  }

  @media only screen and (max-width: 24em) {
    --icon-width: 2.8rem;
  }
}

.icon-external-link {
  width: 2rem;
  height: 2rem;
  fill: var(--color-secondary);
}

a[class*="navItemActive"] .navIcon {
  fill: white;
}

.navIcon {
  fill: var(--color-secondary-light);
}
