.contact {

  margin-bottom: 3.1rem;
  font-size: 2.0rem;

  display: flex;
  flex-direction: column;
  align-items: flex-start;

  &Title {
    font-size: 2.5rem;
    color: var(--color-secondary-light);
    font-weight: 300;
  }

  p,
  a:link,
  a:visited {
    text-decoration: none;
    color: var(--color-secondary);;
  }

  a:hover,
  a:active {
    border-bottom: 2px solid var(--color-primary);
  }
}