.container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 15rem;

  @media only screen and (max-width: 41.25em) {
    height: 80vh;
  }
}

.heroContainer {
  position: relative;
  width: 100%;
  height: 100%;
}

.logo {
  margin-top: 7.5rem;

  @media only screen and (max-width: 55em) {
    margin-top: 10rem;
  }
}

.headingPrimary {
  font-size: 3.9rem;
  color: var(--color-dark);
  font-weight: 300;
  line-height: 130%;
  white-space: nowrap;
  margin-top: 11.9rem;

  @media only screen and (max-width: 55em) {
    margin-top: 10rem;
    font-size: 3.4rem;
  }

  @media only screen and (max-width: 24em) {
    font-size: 3rem;
  }
}

.highlighter {
  position: relative;

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    height: 2.1rem;
    width: 100%;
    z-index: -1;
    background-color: hsla(94, 64%, 60%, 0.28);
  }
}