}

.headingSecondary {
  color: var(--color-secondary);
  font-weight: 400;
  font-size: 3.9rem;
  line-height: 130%;
  text-transform: capitalize;
  letter-spacing: -0.07em;
  margin-bottom: 6.1rem;
}

.highlighter {
  position: relative;

  &::after {
    content: "";
    position: absolute;
    bottom: 2px;
    left: 0;
    height: 2.1rem;
    width: 100%;
    z-index: -1;
    background-color: hsla(94, 64%, 60%, 0.28);
  }
}