.tag {
  display: inline-block;
  padding: 0.7rem 1.2rem;
  border-radius: 11px;
  background-color: var(--color-primary);
  color: var(--color-secondary);
  text-transform: capitalize;
  font-size: 1.28rem;
  font-weight: 600;
  margin: 1rem 1rem 0 0;

}

.react {
  background-color: hsl(193, 95%, 69%);
}

.firebase {
  background-color: hsl(40, 85%, 57%);
}

.html {
  background-color: hsl(3, 74%, 66%);
}

.css {
  background-color: hsl(209, 71%, 55%);
}

.javascript {
  background-color: hsl(48, 79%, 59%);
}

.redux {
  background-color: hsl(263, 46%, 70%);
}

.scss {
  background-color: hsl(330, 53%, 60%);
}

.stripe {
  background-color: hsl(216, 60%, 70%);
}

.styled-components {
  background-color: hsl(19, 60%, 64%);
}

.css-modules {
  background-color: hsl(0, 5%, 85%);
}

.adobe-illustrator {
  background-color: hsl(23, 85%, 57%);
}
