.project {

  font-size: 2rem;
  margin-bottom: 11.9rem;

  display: flex;
  flex-direction: column;
  align-items: flex-start;

  &Title {
    font-weight: 600;
    color: var(--color-secondary);
    margin-bottom: 1.6rem;
  }

  &Description {
    font-weight: 300;
    margin-bottom: 3.1rem;
    color: var(--color-secondary);
  }

  &Tags {
    margin-bottom: 3.1rem;
  }

  &Image {
    max-width: 50%;
    box-shadow: 0 4px 25px var(--box-shadow);
    margin: 0 auto;
  }

  &Link {
    align-self: center;
    margin-top: 3.1rem;

    a:link,
    a:visited {
      text-decoration: none;
      color: var(--color-dark);
      border-bottom: 2px solid var(--color-primary);
    }

    a:hover,
    a:active {
      color: var(--color-dark);
    }
  }
}